<template>
  <div
    class="table__wrap"
    :class="{
      'table__transparent': type === 'transparent',
      'table__bordered': type === 'bordered',
      'table__default': type === 'default'
    }"
  >
    <div
      class="table"
      :class="{
        'table__fit-content': fitContent,
      }"
    >
      <div class="table__head">
        <div class="table__row">
          <div
            v-for="item in columns"
            :key="item.id"
            class="table__head__column table__column"
            :style="{ '--width': (item.width ? item.width + 'px' : 'unset') }"
          >
            <slot name="column" :column="item" />
          </div>
          <div v-if="expand" class="table__head__column table__column" />
        </div>
      </div>
      <div class="table__body">
        <template v-for="(item, i) in data" :key="item.id">
          <Container class="table__row">
            <div
              v-for="col in columns"
              :key="col.id"
              class="table__row__cell table__column"
              :style="{
             '--width': col.width ? col.width + 'px' : 'unset',
             '--overflow-cell': col.width && !col.type ? 'hidden' : 'unset'
            }"
            >
              <div class="table__row__cell-wrap">
                <template v-if="col.icon">
                  <Icon v-if="item[col.name]?.icon" :name="item[col.name]?.icon" class="table__row__cell-icon" />
                  <span v-else :style="{ marginRight: col.icon + 16 + 'px' }" />
                </template>
                <slot name="cell" :item="(item[col.name]?.cellValue || item[col.name])" :column="col" />
                <Icon
                  v-if="col.copy"
                  class="table__row__cell-copy"
                  name="copy"
                  @click="$emit('copy-cell', { col, item })"
                />
              </div>
            </div>
            <div
              v-if="expand"
              class="table__row__cell table__column"
              @click="onExpand(i)"
            >
              <Icon name="arrow-down" width="9" height="7" />
            </div>
          </Container>
          <div v-if="expandIndex === i" class="table__expand-row">
            <td colspan="20">
              <slot name="expand" :item="item"/>
            </td>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import Icon from '@/components/Icon/Icon'
import Container from '@/components/Container'

export default {
  name: 'Table',
  components: { Container, Icon },
  props: {
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    expand: {
      type: Boolean,
      default: false
    },
    fitContent: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'bordered'
    }
  },
  setup () {
    const expandIndex = ref(-1)

    const onExpand = (i) => {
      expandIndex.value = expandIndex.value === i ? -1 : i
    }

    return {
      expandIndex,
      onExpand
    }
  }
}
</script>

<style lang="scss">
@import "table";
</style>
